<template>
  <view class="edit-product-container">
    <view class="form-container">
      <view class="form-group">
        <text class="form-label">ÂïÜÂìÅÂêçÁß∞ *</text>
        <input class="form-input" v-model="productForm.name" placeholder="ËØ∑ËæìÂÖ•ÂïÜÂìÅÂêçÁß∞" maxlength="50" />
      </view>
      <view class="form-group">
        <text class="form-label">ÂïÜÂìÅÂàÜÁ±ª *</text>
        <picker class="form-picker" :value="categoryIndex" :range="categories" @change="onCategoryChange">
          <view class="picker-text">{{ categories[categoryIndex] || 'ËØ∑ÈÄâÊã©ÂàÜÁ±ª' }}</view>
        </picker>
      </view>
      <view class="form-group">
        <text class="form-label">ÂïÜÂìÅ‰ª∑Ê†º *</text>
        <input class="form-input" type="digit" v-model="productForm.price" placeholder="ËØ∑ËæìÂÖ•‰ª∑Ê†º" />
      </view>
      <view class="form-group">
        <text class="form-label">ÂïÜÂìÅÊèèËø∞</text>
        <textarea class="form-textarea" v-model="productForm.description" placeholder="ËØ∑ËæìÂÖ•ÂïÜÂìÅÊèèËø∞" maxlength="200" />
      </view>
      <view class="form-group">
        <text class="form-label">ÂïÜÂìÅÂõæÁâá</text>
        <view class="image-upload-section">
          <view class="image-preview" v-if="productForm.image_url">
            <image :src="productForm.image_url" mode="aspectFill" class="preview-image" />
            <view class="image-actions">
              <button class="image-btn change" @click="chooseImage">Êõ¥Êç¢ÂõæÁâá</button>
              <button class="image-btn delete" @click="removeImage">Âà†Èô§</button>
            </view>
          </view>
          <view class="upload-placeholder" v-else @click="chooseImage">
            <text class="upload-icon">üì∑</text>
            <text class="upload-text">ÁÇπÂáª‰∏ä‰º†ÂõæÁâá</text>
            <text class="upload-tip">ÊîØÊåÅ JPG„ÄÅPNG Ê†ºÂºè</text>
          </view>
        </view>
      </view>
      <view class="form-group">
        <text class="form-label">ÂïÜÂìÅÁä∂ÊÄÅ</text>
        <switch :checked="productForm.status" @change="onStatusChange" color="#667eea" />
        <text class="status-text">{{ productForm.status ? '‰∏äÊû∂' : '‰∏ãÊû∂' }}</text>
      </view>
    </view>
    <view class="action-buttons">
      <button class="action-btn cancel" @click="goBack">ÂèñÊ∂à</button>
      <button class="action-btn submit" @click="submitForm" :disabled="!isFormValid">‰øùÂ≠ò‰øÆÊîπ</button>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      productId: null,
      productForm: {
        name: '',
        category: '',
        price: '',
        description: '',
        image_url: '',
        status: true
      },
      categories: ['ÊãõÁâåÂ•∂Ëå∂', 'ÁúüÈ≤úÊûúËå∂', 'ÈÖ∏Â•∂Â•∂Êòî', 'Â∞èÊñôÂå∫', '‰øùÊ∏©Ë¢ãÂå∫'],
      categoryIndex: -1
    };
  },
  computed: {
    isFormValid() {
      return this.productForm.name.trim() &&
        this.productForm.category &&
        this.productForm.price &&
        !isNaN(parseFloat(this.productForm.price));
    }
  },
  onLoad(options) {
    if (options.productId) {
      this.productId = options.productId;
      this.loadProduct();
    }
  },
  methods: {
    // Âä†ËΩΩÂïÜÂìÅ‰ø°ÊÅØ
    loadProduct() {
      uni.request({
        url: `http://localhost:3000/api/admin/products?page=1&limit=1000`, // ÁÆÄÂçïÊãâÂèñÂÖ®ÈÉ®ÔºåÂÆûÈôÖÂèØ‰ºòÂåñ‰∏∫Âçï‰∏™ÂïÜÂìÅÊé•Âè£
        method: 'GET',
        header: { 'Content-Type': 'application/json' },
        success: (res) => {
          if (res.data && res.data.success) {
            const product = res.data.products.find(p => p.id == this.productId);
            if (product) {
              this.productForm = {
                name: product.name,
                category: product.category,
                price: product.price,
                description: product.description,
                image_url: product.image_url,
                status: !!product.status
              };
              this.categoryIndex = this.categories.indexOf(product.category);
            }
          }
        }
      });
    },
    onCategoryChange(e) {
      this.categoryIndex = e.detail.value;
      this.productForm.category = this.categories[this.categoryIndex];
    },
    onStatusChange(e) {
      this.productForm.status = e.detail.value;
    },
    submitForm() {
      if (!this.isFormValid) {
        uni.showToast({ title: 'ËØ∑Â°´ÂÜôÂÆåÊï¥‰ø°ÊÅØ', icon: 'none' });
        return;
      }
      uni.request({
        url: `http://localhost:3000/api/admin/products/${this.productId}`,
        method: 'PUT',
        header: {
          'Content-Type': 'application/json'
        },
        data: {
          name: this.productForm.name.trim(),
          category: this.productForm.category,
          price: parseFloat(this.productForm.price),
          description: this.productForm.description.trim(),
          image_url: this.productForm.image_url.trim(),
          status: this.productForm.status ? 1 : 0
        },
        success: (res) => {
          if (res.data && res.data.success) {
            uni.showToast({ title: '‰øùÂ≠òÊàêÂäü', icon: 'success' });
            setTimeout(() => {
              uni.navigateBack();
            }, 1200);
          } else {
            uni.showToast({ title: res.data.message || '‰øùÂ≠òÂ§±Ë¥•', icon: 'none' });
          }
        },
        fail: () => {
          uni.showToast({ title: 'ÁΩëÁªúÈîôËØØ', icon: 'none' });
        }
      });
    },
    goBack() {
      uni.navigateBack();
    },

    // ÈÄâÊã©ÂõæÁâá
    chooseImage() {
      uni.chooseImage({
        count: 1,
        sizeType: ['compressed'],
        sourceType: ['album', 'camera'],
        success: (res) => {
          const filePath = res.tempFilePaths[0];
          this.uploadImage(filePath);
        },
        fail: () => {
          uni.showToast({ title: 'ÈÄâÊã©ÂõæÁâáÂ§±Ë¥•', icon: 'none' });
        }
      });
    },

    // ‰∏ä‰º†ÂõæÁâá
    uploadImage(filePath) {
      uni.showLoading({ title: '‰∏ä‰º†‰∏≠...' });
      
      uni.uploadFile({
        url: 'http://localhost:3000/api/admin/upload-product-image',
        filePath: filePath,
        name: 'file',
        success: (uploadRes) => {
          uni.hideLoading();
          const data = JSON.parse(uploadRes.data);
          if (data.success) {
            this.productForm.image_url = data.url;
            uni.showToast({ title: '‰∏ä‰º†ÊàêÂäü', icon: 'success' });
          } else {
            uni.showToast({ title: data.message || '‰∏ä‰º†Â§±Ë¥•', icon: 'none' });
          }
        },
        fail: () => {
          uni.hideLoading();
          uni.showToast({ title: '‰∏ä‰º†Â§±Ë¥•', icon: 'none' });
        }
      });
    },

    // Âà†Èô§ÂõæÁâá
    removeImage() {
      uni.showModal({
        title: 'Á°ÆËÆ§Âà†Èô§',
        content: 'Á°ÆÂÆöË¶ÅÂà†Èô§ËøôÂº†ÂõæÁâáÂêóÔºü',
        success: (res) => {
          if (res.confirm) {
            this.productForm.image_url = '';
            uni.showToast({ title: 'ÂõæÁâáÂ∑≤Âà†Èô§', icon: 'success' });
          }
        }
      });
    }
  }
};
</script>

<style scoped>
.edit-product-container { min-height: 100vh; background: #f5f5f5; padding: 20rpx; }
.form-container { background: #fff; border-radius: 16rpx; padding: 30rpx; margin-bottom: 40rpx; box-shadow: 0 2rpx 8rpx rgba(0,0,0,0.1); }
.form-group { margin-bottom: 30rpx; }
.form-label { display: block; font-size: 28rpx; color: #333; margin-bottom: 16rpx; font-weight: 500; }
.form-input { width: 100%; height: 80rpx; border: 2rpx solid #e5e5e5; border-radius: 12rpx; padding: 0 20rpx; font-size: 28rpx; background: #f8f9fa; box-sizing: border-box; }
.form-input:focus { border-color: #667eea; background: #fff; }
.form-picker { width: 100%; height: 80rpx; border: 2rpx solid #e5e5e5; border-radius: 12rpx; background: #f8f9fa; display: flex; align-items: center; padding: 0 20rpx; }
.picker-text { font-size: 28rpx; color: #333; }
.form-textarea { width: 100%; height: 120rpx; border: 2rpx solid #e5e5e5; border-radius: 12rpx; padding: 20rpx; font-size: 28rpx; background: #f8f9fa; box-sizing: border-box; resize: none; }
.form-textarea:focus { border-color: #667eea; background: #fff; }
.form-group:last-child { display: flex; align-items: center; gap: 20rpx; }
.status-text { font-size: 28rpx; color: #666; }
.action-buttons { display: flex; gap: 20rpx; }
.action-btn { flex: 1; height: 88rpx; border-radius: 12rpx; font-size: 32rpx; font-weight: 500; border: none; }
.action-btn.cancel { background: #fff; color: #666; border: 2rpx solid #e5e5e5; }
.action-btn.submit { background: #667eea; color: #fff; }
.action-btn.submit:disabled { background: #ccc; }

.image-upload-section {
  width: 100%;
}

.image-preview {
  position: relative;
  width: 100%;
  height: 300rpx;
  border-radius: 12rpx;
  overflow: hidden;
  background: #f8f9fa;
  border: 2rpx solid #e5e5e5;
}

.preview-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.image-actions {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.7);
  padding: 20rpx;
  display: flex;
  gap: 20rpx;
}

.image-btn {
  flex: 1;
  height: 60rpx;
  border-radius: 8rpx;
  font-size: 24rpx;
  border: none;
}

.image-btn.change {
  background: #667eea;
  color: #fff;
}

.image-btn.delete {
  background: #dc3545;
  color: #fff;
}

.upload-placeholder {
  width: 100%;
  height: 300rpx;
  border: 2rpx dashed #e5e5e5;
  border-radius: 12rpx;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: #f8f9fa;
  cursor: pointer;
  transition: all 0.3s ease;
}

.upload-placeholder:active {
  background: #e9ecef;
  border-color: #667eea;
}

.upload-icon {
  font-size: 60rpx;
  margin-bottom: 20rpx;
}

.upload-text {
  font-size: 28rpx;
  color: #333;
  margin-bottom: 10rpx;
}

.upload-tip {
  font-size: 24rpx;
  color: #999;
}
</style> 